import React, { Component } from 'react';
import 'bootstrap/dist/css/bootstrap.css'
import '../src/assets/css/style.css'
import Header from './components/Header';
import TotalCovid from './components/TotalCovid';
import Footer from './components/Footer';

class App extends Component {
  constructor() {
    super();
    this.state = {
      country: [],
      isLoaded: false
    };
  }


  componentDidMount() {
    fetch("https://corona.lmao.ninja/countries/Bangladesh")
      .then(res => res.json())
      .then(json => {
        this.setState({
          isLoaded: true,
          country: json
        });
      });
  }

  render() {
    var { country } = this.state;
    return (
      <div>
        <Header/>
        <div className="container">
          <div className="row">
            <div className="col-xl-12">
              <div className="title-heading text-center p-t-b-25">
              <h3>COVID 19 (CORONA VIRUS) UPDATE BANGLADESH</h3>
              </div>
            </div>
          </div>
          <div className="row stats-p">
            <div className="col text-center">
              <div className="stats-title">
                <p>এখন পর্যন্ত আক্রান্ত</p>
              </div>
              <div className="stats">
                <p>{country.cases}</p>
              </div>
            </div>
            <div className="col text-center">
              <div className="stats-title">
                <p>এখন পর্যন্ত মৃত</p>
              </div>
              <div className="stats death">
                <p>{country.deaths}</p>
              </div>
            </div>
            <div className="col text-center">
              <div className="stats-title">
                <p>খুব খারাপ অবস্থা</p>
              </div>
              <div className="stats today">
                <p>{country.critical}</p>
              </div>
            </div>
            <div className="col text-center">
              <div className="stats-title">
                <p>এখনো সুস্থ হয়নি</p>
              </div>
              <div className="stats active">
                <p>{country.active}</p>
              </div>
            </div>
            <div className="col text-center">
              <div className="stats-title">
                <p>এখন পর্যন্ত সুস্থ হয়েছে</p>
              </div>
              <div className="stats recovered">
                <p>{country.recovered}</p>
              </div>
            </div>
          </div>
          <div className="row stats-pp">
            <TotalCovid/>
          </div>
        </div>
        <Footer/>
      </div>
    )
  }
}

export default App;